<template>
  <div v-loading='loading' class="all">
    <cms-breadcrumb to="/wechatPublic/list">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt>
        <li>系统接入公众号统一采用授权方式接入，无法进行修改信息</li>
      </cms-prompt>
      <el-form ref="form" :model="formData" label-width="90px" style="width: 60%;margin: 0 auto;">
        <div class="product-item-one">
          <el-row style="position:relative;">
            <el-col class="product-item-one-el">
              <div class="product-item-one-zhong" >
                <div class="product-img">
                  <img class="product-img-img" :src="formData.headImg">
                </div>
                <div class="product-desc">
                  <div class="product-name">
                    {{formData.wechatName}}
                  </div>
                  <div class="product-attr">
                    <span>主体信息：
                      <span v-text="formData.principalName"></span>
                      <i class="iconfont"></i>
                    </span>
                     <span class="li-span">|</span>
                    <span>拥有者：
                      <span v-text="formData.owerType==1?'平台':'商家'"></span>
                      <i class="iconfont"></i>
                    </span>
                     <span class="li-span">|</span>
                    <span style="margin-left:10px" v-if="formData.grantType=== 1">
                      <i class="iconfont icon-gou success" style="color:green"></i>
                      <span>授权接入</span>
                    </span>
                  </div>
                </div>
                <div class="product-btn">
                  <cms-button type='info' icon='warning' slot="reference" @click="cancelJurisdiction(formData.id)">取消授权</cms-button>
              </div>
              </div>
            </el-col>
          </el-row>
        </div>
        <h3 class="cms-segmentation" style="margin-top:20px">公众号基本信息</h3>
        <div>
          <el-form-item lable-width="0px" label="头像" prop="headImg" >
            <img class="product-img-img product-img-img-two" :src="formData.headImg">
          </el-form-item>
          <el-form-item label="二维码" prop="qrcodeUrl" >
            <img class="product-img-img product-img-img-two" :src="$cms.weChatUrl(formData.qrcodeUrl)">
          </el-form-item>
          <el-form-item label="原始ID" prop="globalId" >
            {{formData.globalId}}
          </el-form-item>
          <el-form-item label="AppId" prop="appId" >
            {{formData.appId}}
          </el-form-item>
          <el-form-item label="类型" prop="type" >
            <span v-text="formData.type==1?'公众号':'小程序'"></span>
          </el-form-item>
          <el-form-item label="接入方式" prop="grantType" >
            <span v-text="formData.grantType==1?'授权接入':''"></span>
          </el-form-item>
          <el-form-item label="认证状态" prop="verifyStatus" >
            <span v-text="formData.verifyStatus==1?'未认证':'已认证'"></span>
            <span v-if="formData.verifyStatus === 1">
              当前公众号未认证，认证后请
              <span class="green">重新授权</span>
            </span>
          </el-form-item>
        </div>
        <div class="product-item">
          <h3 class="cms-segmentation">授权信息（如出现功能异常或提示无权限，请尝试重新授权，赋予所有权限，
            <span class="green" @click="getLink">重新授权</span>）</h3>
          <el-row style="margin:auto">
            <div class="product-p" v-for="(item, index) in funcInfo" :key="index">
              <p class=""
              :class="formData.funcInfo.indexOf(index+1) > -1 ?'product-img-p1' :'product-img-p2'"
               v-text="formData.funcInfo.indexOf(index+1) > -1?'已授权' : '未授权'">
               </p>
              <span>{{item}}</span>
            </div>
          </el-row>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
import form from '@/mixins/form.js'
import {
  weChatListGet,
  weChatSetCancel
} from '@/api/wechat/index.js'
var defaultData = {
  id: '',
  alias: '',
  appId: '',
  createTime: '',
  createUser: '',
  funcInfo: '',
  globalId: '',
  grantType: '',
  headImg: '',
  network: '',
  owerType: '',
  principalName: '',
  qrcodeUrl: '',
  storeId: '',
  type: '',
  updateTime: '',
  updateUser: '',
  verifyStatus: '',
  wechatName: '',
  wechatType: ''
}
export default {
  name: 'publicSetting',
  mixins: [form],
  data() {
    return {
      jurisdiction: [],
      funcInfo: [
        '消息管理权限',
        '用户管理权限',
        '帐号服务权限',
        '网页服务权限',
        '微信小店权限',
        '微信多客服权限',
        '群发与通知权限',
        '微信卡券权限',
        '微信扫一扫权限',
        '微信连WIFI权限',
        '素材管理权限',
        '微信摇周边权限',
        '微信门店权限',
        '微信支付权限',
        '自定义菜单权限',
        '帐号管理权限',
        '开发管理权限',
        '客服消息管理权限'
      ],
      formData: Object.assign({}, defaultData),
      baseUrl: process.env.baseUrl
    }
  },
  methods: {
    getFormData(id) {
      // 通过id获取表单信息
      weChatListGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
      })
    },
    // 重新授权
    getLink() {
      this.$confirm(
        '为保证重新授权前后数据正常，请使用同一公众号进行重新授权为保证系统能正常运行所有与微信平台的对接，请在授权时授予所有权限',
        '系统提示',
        {
          confirmButtonText: '已了解，确认执行',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(() => {
          const body = document.querySelector('body')
          const link = document.createElement('a')
          link.href = this.baseUrl + '/weChat/grantAuth'
          link.setAttribute('target', '_blank')
          body.appendChild(link)
          link.click()
        })
        .catch(() => {})
    },
    // 取消授权
    cancelJurisdiction(id) {
      this.$confirm(
        '解除绑定会导致当前公众号设置的信息丢失（含素材、自定义菜单、自动回复、定时群发、粉丝信息等），请确认是否继续？',
        '系统提示',
        {
          confirmButtonText: '已了解解绑风险，确认执行',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(() => {
          weChatSetCancel({ ids: [id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('取消授权成功')
              this.getTableData(this.callback, this.queryParams, this.isPage)
              this.$routerLink('/wechat/publicList/list', this.queryParams)
            } else {
              this.$errorMessage('取消授权失败,' + res.message)
            }
          })
        })
        .catch(() => {})
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>


<style scoped>
.el-form-item__label {
    width: 101px!important;
}
.product-item-one{
  width: 100%;
  margin-top: 30px;
  height: 130px;
  background-color: #ffffff;
	border-radius: 12px;
  -moz-box-shadow:2px 2px 10px #999999;
  -webkit-box-shadow:2px 2px 10px #999999;
  box-shadow:0px 2px 16px #999999;
  min-width:545px!important;
}
.cms-layout .el-form-item {
    border-bottom: 1px solid #f7f7f7;
}
.el-row {
  height: 100%;
  font-size: 13px;
  color: rgb(96, 98, 102);
}
.product-img {
  float: left;
}
.product-p span {
  line-height: 30px;
}
.product-p {
  float: left;
  margin-top: 10px;
  width: 160px;
  margin-left: 15px;
  margin-right: 23px;
  line-height: 20px;
}
.product-desc {
  float: left;
  margin-left: 30px;
    font-size: 13px;
  color: rgb(96, 98, 102);
}
.product-btn {
  position: absolute;
  top:38px;
  right: 20px;
}
.product-img-p1 {
  float: left;
 margin-right: 3px;
 width: 42px;
  height: 18px;
  margin-top: 5px;
  color: #00da38;
	background-color: #ffffff;
	border-radius: 3px;
  border: solid 1px #00da38;
   text-align: center;
  line-height: 18px;
}
.product-img-p2 {
  float: left;
  margin-right: 3px;
 width: 42px;
 margin-top: 5px;
	height: 18px;
	background-color: #ffffff;
  border-radius: 3px;
  text-align: center;
  line-height: 18px;
	border: solid 1px #999999;
}
.product-name {
  margin-top: 30px;
	height: 15px;
	font-size: 18px;
	line-height: 24px;
	color: #444444;
}
.product-attr {
  margin-top: 20px;
}
.product-img-img {
  width: 80px;
	height: 80px;
  margin-top: 20px;
	border-radius: 12px;
	border: solid 2px #f4f8fb;
}
.product-img-img-two{
  margin-top: 0px;
}
.green {
  text-decoration: underline;
	font-size: 14px;
	color: #188ae2;
  cursor: pointer;
}
.cms-form-layout {
  padding: 16px 16px 16px 16px;
  margin: 0 auto;
  width: 100%;
}
.product-item-one-el{
margin-top: 7px;
display: block;
width:100%; padding: 0 20px 0 20px;display:block;min-width:516px!important;
}
.product-item-one-zhong{
  width:100%;min-width:516px
}
.li-span{
  color: #e7e7ee;
  margin: 0 15px 0 15px;
}
</style>



